import { Component, Input, OnChanges, SimpleChanges } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { distinctUntilChanged, shareReplay, switchMap, tap } from 'rxjs/operators';
import { ImageWidgetConfig, ImageWidgetStyle } from '../image-widget.model';
import { ImageWidgetService } from '../image-widget.service';

@Component({
  selector: 'c8y-image-widget-view',
  templateUrl: './image-widget-view.component.html'
})
export class ImageWidgetViewComponent implements OnChanges {
  @Input() config: ImageWidgetConfig;
  imageBinaryId$ = new BehaviorSubject<string>(null);
  imageDetails$: Observable<{
    file: File;
    base64: string;
  }>;
  styling: ImageWidgetStyle | null;
  loading = true;

  constructor(private imageWidget: ImageWidgetService) {
    this.imageDetails$ = this.imageBinaryId$.pipe(
      distinctUntilChanged(),
      tap(() => (this.loading = true)),
      switchMap(imageBinaryId => this.imageWidget.getImageDetails(imageBinaryId)),
      tap(() => (this.loading = false)),
      shareReplay(1)
    );
  }

  ngOnChanges(changes: SimpleChanges): void {
    if (changes.config) {
      const value = changes.config.currentValue as ImageWidgetConfig;
      this.imageBinaryId$.next(value.imageBinaryId);
      this.setStyling(value);
    }
  }

  private setStyling(config: ImageWidgetConfig): void {
    this.styling = this.imageWidget.getStyling(config);
  }
}
