import { Injectable } from '@angular/core';
import { IManagedObject } from '@c8y/client';
import { InventoryBinaryService } from '@c8y/client';
import { IManagedObjectBinary, InventoryService } from '@c8y/client';
import { AlertService, FilesService, gettext } from '@c8y/ngx-components';
import { TranslateService } from '@ngx-translate/core';
import {
  defaultObjectPositionValue,
  ImageWidgetConfig,
  ImageWidgetStyle
} from './image-widget.model';

@Injectable({ providedIn: 'root' })
export class ImageWidgetService {
  constructor(
    private inventory: InventoryService,
    private fileService: FilesService,
    private alert: AlertService,
    private translate: TranslateService,
    private binary: InventoryBinaryService
  ) {}

  async getImageDetails(imageBinaryId: string | null): Promise<{ file: File; base64: string }> {
    if (!imageBinaryId) {
      return null;
    }

    try {
      const { data: imageBinaryMo } = await this.inventory.detail(imageBinaryId);
      const file = await this.fileService.getFile(imageBinaryMo as IManagedObjectBinary);
      const base64 = await this.fileService.toBase64(file);
      return {
        file,
        base64: base64 as string
      };
    } catch (e) {
      const text = this.translate.instant(
        gettext('Unable to retrieve image with id: {{ imageBinaryId }}'),
        { imageBinaryId }
      );
      this.alert.danger(text, e?.data);
    }

    return null;
  }

  async uploadFile(
    file: File,
    options: {
      dashboardMoId?: string;
      isDeviceTypeDashboard?: boolean;
    } = {}
  ): Promise<string> {
    const hash = await this.fileService.getHashSumOfFile(file);
    const fileDetails = { c8y_SHA256: hash };
    const globalImage = !options?.dashboardMoId || options?.isDeviceTypeDashboard;
    const partialBinaryMo: Partial<IManagedObject> = !globalImage
      ? { ...fileDetails }
      : { ...fileDetails, c8y_Global: {} };
    const { data: mo } = await this.binary.create(file, partialBinaryMo);
    if (options?.dashboardMoId) {
      await this.inventory.childAdditionsAdd(mo, options?.dashboardMoId);
    }
    return mo.id;
  }

  getStyling(config: ImageWidgetConfig): ImageWidgetStyle | null {
    if (!config.styling?.objectFit || config.styling?.objectFit === 'full-width') {
      return null;
    }
    return {
      'object-fit': config.styling.objectFit,
      'object-position': `${config.styling?.objectPositionX || defaultObjectPositionValue} ${
        config.styling?.objectPositionY || defaultObjectPositionValue
      }`
    };
  }
}
