import { Component, Input, OnDestroy, OnInit, Optional } from '@angular/core';
import { WidgetConfigComponent } from '@c8y/ngx-components/context-dashboard';
import {
  C8yValidators,
  OnBeforeSave,
  FilesService,
  AlertService,
  gettext
} from '@c8y/ngx-components';
import { get } from 'lodash-es';
import {
  defaultObjectFitValue,
  defaultObjectPositionValue,
  ImageWidgetConfig,
  ImageWidgetConfigStyling,
  ImageWidgetStyle
} from '../image-widget.model';
import { ControlContainer, FormBuilder, FormGroup, NgForm, Validators } from '@angular/forms';
import { BehaviorSubject, Observable, merge, Subject } from 'rxjs';
import {
  distinctUntilChanged,
  filter,
  map,
  shareReplay,
  switchMap,
  takeUntil,
  tap
} from 'rxjs/operators';
import { ImageWidgetService } from '../image-widget.service';

@Component({
  selector: 'c8y-image-widget-config',
  templateUrl: './image-widget-config.component.html',
  viewProviders: [{ provide: ControlContainer, useExisting: NgForm }]
})
export class ImageWidgetConfigComponent implements OnInit, OnDestroy, OnBeforeSave {
  @Input() config: ImageWidgetConfig;
  imageBinaryId$ = new BehaviorSubject<string>(null);
  imageSrc$: Observable<string>;
  loading = false;
  formGroup: FormGroup;
  fileFromConfig: File;
  styling: ImageWidgetStyle | null;
  destroyed$ = new Subject<void>();
  objectFitOptions = [
    {
      label: gettext('Contain`verb, image fitting option`'),
      value: 'contain',
      description: gettext(
        'The image is entirely displayed within the widget while preserving the aspect ratio.'
      )
    },
    {
      label: gettext('Cover`verb, image fitting option`'),
      value: 'cover',
      description: gettext(
        'The image is resized to fill the widget while preserving the aspect ratio. Overflowing areas are clipped.'
      )
    },
    {
      label: gettext('Fill`verb, image fitting option`'),
      value: 'fill',
      description: gettext(
        'The image is stretched to fill the widget, overriding the aspect-ratio.'
      )
    },
    {
      label: gettext('Full width`image fitting option`'),
      value: 'full-width',
      description: gettext(
        `The image is resized to fit the widget's width while preserving the aspect ratio. Overflowing area is scrollable.`
      )
    }
  ];

  constructor(
    private formBuilder: FormBuilder,
    private form: NgForm,
    private fileService: FilesService,
    private alert: AlertService,
    private imageWidget: ImageWidgetService,
    @Optional() private widgetConfig: WidgetConfigComponent
  ) {}

  async onBeforeSave(config?: ImageWidgetConfig): Promise<boolean> {
    if (this.formGroup.invalid) {
      return false;
    }

    const fileFromForm = this.getFileFromFormValue(this.formGroup.value);
    if (fileFromForm && fileFromForm !== this.fileFromConfig) {
      try {
        const imageBinaryId = await this.imageWidget.uploadFile(fileFromForm, {
          dashboardMoId: this.getDashboardMoId(),
          isDeviceTypeDashboard: this.widgetConfig?.isDeviceTypeDashboard
        });
        Object.assign(config, { imageBinaryId });
      } catch (e) {
        this.alert.danger(gettext('Unable to upload image.'), e?.data);
        return false;
      }
    }
    const styling: ImageWidgetConfigStyling = this.formGroup.value.styling;
    Object.assign(config, { styling });

    return true;
  }

  ngOnInit() {
    this.initForm();
    const imageFromConfig = this.imageBinaryId$.pipe(
      filter(id => !!id),
      distinctUntilChanged(),
      tap(() => (this.loading = true)),
      switchMap(imageBinaryId => this.imageWidget.getImageDetails(imageBinaryId)),
      tap(details => {
        this.loading = false;
        this.fileFromConfig = details.file;
        if (details) {
          this.formGroup.patchValue({ images: [{ file: details.file, name: details.file.name }] });
        }
      }),
      map(details => details?.base64),
      shareReplay(1)
    );
    const selectedFile = this.formGroup.valueChanges.pipe(
      map(value => this.getFileFromFormValue(value)),
      distinctUntilChanged(),
      switchMap(file => (file ? this.fileService.toBase64(file) : Promise.resolve('')))
    );
    this.imageSrc$ = merge(imageFromConfig, selectedFile).pipe(
      map(base64 => (base64 ? (base64 as string) : ''))
    );
    this.imageBinaryId$.next(this.config?.imageBinaryId);
  }

  ngOnDestroy(): void {
    this.destroyed$.next();
    this.destroyed$.complete();
  }

  private getDashboardMoId(): string | null {
    return get(this.widgetConfig, 'mo.id', null);
  }

  private async initForm(): Promise<void> {
    const stylingFormGroup = this.formBuilder.group({
      objectFit: [
        // fallback to 'full-width' for old legacy widgets, newly created widgets should use defaultObjectFitValue
        this.config.imageBinaryId && !this.config.styling ? 'full-width' : defaultObjectFitValue,
        [Validators.required]
      ],
      objectPositionX: [defaultObjectPositionValue, [Validators.required]],
      objectPositionY: [defaultObjectPositionValue, [Validators.required]]
    });
    this.formGroup = this.formBuilder.group({
      images: [
        null,
        [
          Validators.required,
          Validators.minLength(1),
          Validators.maxLength(1),
          C8yValidators.filesValidator({ maximumFileSizeInKb: 1000, typePrefix: 'image/' })
        ]
      ],
      styling: stylingFormGroup
    });
    this.form.form.addControl('config', this.formGroup);
    this.formGroup.patchValue(this.config);
    this.formGroup.valueChanges
      .pipe(takeUntil(this.destroyed$))
      .subscribe(changes => this.setStyling(changes));
  }

  private getFileFromFormValue(formValue: any): File | null {
    const images: any[] = formValue?.images || [];
    return images[0]?.file || null;
  }

  private setStyling(config: ImageWidgetConfig): void {
    this.styling = this.imageWidget.getStyling(config);
  }
}
